<template>
    <div>

        <mt-header fixed title="">
            <div v-on:click="$router.back()" slot="left">
                <mt-button icon="back"></mt-button>
            </div>
            <mt-button icon="more" slot="right"></mt-button>
        </mt-header>

        <div class="content">

            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="items in swipeItems">
                        <!-- <h4>1111</h4> -->
                        <img :src="items.src" alt="">

                    </div>
                </div>
                <div class="swiper-pagination"></div>
            </div>
            <div class="productDetails">
                <div>
                    <span class="product_pice">250</span>
                    <span class="cuxiaojia"></span>
                </div>
                <div class="fs-13 c-A2A2A2 mt-5">
                    <span></span>
                    <del>499</del>
                </div>
                <div class="df fs-13 c-A2A2A2 mt-10 pb-10 porductType">
                    <div class="fg-1">7.00</div>
                    <div class="fg-1 tx-c">3791</div>
                    <div class="fg-1 tx-r"></div>
                </div>
                <div class="c-303030">
                    <div>
                        <span class="pt_label">APP</span>
                    </div>
                    <div class="product_title">Nintendo Switch NS</div>
                </div>
            </div>

            <div class="productDetails mt-10 c-ff0036">
                <div class="df fw-bold">
                    <div class="icon-shop fs-20"></div>
                    <div class="fs-13 ml-5"></div>
                </div>
                <div class="c-303030 fs-13 mt-5">
                    APP
                </div>
            </div>
            <div class="productDetails df mt-10 c-ff0036" v-on:click="show_parameter">
                <div class="c-303030 fs-13 fg-2">
                    
                </div>
                <div class="fg-1 c-303030 fs-13 tx-r"></div>
            </div>
            <!--  -->
            <div class="productDetails mt-10">
                <div class="fs-13">(963)</div>
                <router-link to="goods/goods_comment" class="products_reviews_type">
                    <span class="high_opinion">(64)</span>
                    <span class="high_opinion">(51)</span>
                    <span class="high_opinion">(50)</span>
                    <span class="high_opinion">(40)</span>
                    <span class="high_opinion">(6)</span>
                    <span class="negative">(18)</span>
                </router-link>
                <!--  -->
                <div class="products_reviews_content mt-10">
                    <productsReviewsList :nickname="productsReviewsList[0].nickname" :headerImg="productsReviewsList[0].headerImg" :details="productsReviewsList[0].details"></productsReviewsList>
                </div>
            </div>
            <footer class="c-fff">
                <div class="df f-ac">
                    <div class="fg-1 c-5F646E">
                        <div class="icon-shop-center fs-25"></div>
                        <div class="fg-1 fs-12"></div>
                    </div>
                    <div id="Collection" class="fg-1 Collection" v-on:click="collection">
                        <div v-bind:class="{'c-ff0036':isCollection}">
                            <div class="icon-collection fs-25"></div>
                            <div class="fg-1 fs-12"></div>
                        </div>

                    </div>
                    <div class="join_shopCar fg-2"></div>
                    <div class="buy fg-2"></div>
                </div>

            </footer>
        </div>

        <!--  -->
        <mt-popup v-model="popupVisible" position="bottom" pop-transition="popup-fade" style="width:100%;">
            <div class="parameter">
                <div class="p-15-0"></div>
                <div class="df parameterType">
                    <div class="parameter_title"></div>
                    <div class="parameter_Detalis"></div>
                </div>
                <div class="df parameterType">
                    <div class="parameter_title"></div>
                    <div class="parameter_Detalis">Nintendo Switch </div>
                </div>
                <div class="df parameterType">
                    <div class="parameter_title"></div>
                    <div class="parameter_Detalis">   </div>
                </div>
                <div class="df parameterType">
                    <div class="parameter_title"></div>
                    <div class="parameter_Detalis">  + + +2 ++ +++ ++8 </div>
                </div>
                <div class="df parameterType">
                    <div class="parameter_title"></div>
                    <div class="parameter_Detalis">32GB </div>
                </div>
            </div>
        </mt-popup>
        <!--  -->
        
    </div>
</template>
<script>
//Swiper.min.js
import Swiper from "../../node_modules/swiper/dist/js/swiper.min.js";
//
import productsReviewsList from "../components/products_reviews_list";
//
import productReviewsType from "../components/product_reviews_type";

export default {
    name: "product_details",
    components: {
        productsReviewsList,
        productReviewsType
    },
    data() {
        return {
            popupVisible: false,
            productsReviews: false,
            //
            count: 0,
            //
            isCollection: false,
            swipeItems: [
                {
                    src: require("../assets/switch.png"),
                    id: "1"
                },
                {
                    src: require("../assets/switch.png"),
                    id: "2"
                },
                {
                    src: require("../assets/switch.png"),
                    id: "3"
                }
            ],
            productsReviewsList: [
                {
                    nickname: "YJSSNF--",
                    headerImg: require("../assets/header.jpg"),
                    details:
                        "         OK  "
                }
            ],

        };
    },
    mounted: function() {
        this.swiper();
    },
    methods: {
        //DOM  ref='XXX';
        // this.$refs.XXX
        swiper() {
            var mySwiper = new Swiper(".swiper-container", {
                autoplay: true, //
                pagination: {
                    el: ".swiper-pagination",
                    // type: "bullets"
                    type: "fraction"
                    // type : 'progressbar'
                    // type : 'custom'
                }
            });
        },
        show_parameter() {
            this.popupVisible = true;
        },
        show_productsReviews() {
            var _self = this;
            if (_self.productsReviews) {
                _self.productsReviews = false;
            } else {
                _self.productsReviews = true;
            }
        },
       
        collection() {
            //

            if (this.isCollection) {
                this.isCollection = false;
            } else {
                this.isCollection = true;
            }
        }
    }
};
</script>
<style scoped>
/* Swiper.min.css */
@import url("../../node_modules/swiper/dist/css/swiper.min.css");

.mint-header.is-fixed {
    z-index: 2;
}
.content {
    margin: 45px 0px 50px 0px;
}
.swiper-slide img {
    width: 100%;
}
.swiper-container-horizontal > .swiper-pagination-bullets[data-v-d81c94aa],
.swiper-pagination-custom[data-v-d81c94aa],
.swiper-pagination-fraction[data-v-d81c94aa] {
    font-weight: bold;
    color: #ff0036;
    font-size: 15px;
}
.productDetails {
    padding: 10px;
    background-color: #fff;
}
.product_pice {
    color: #ff0036;
    font-size: 18px;
    font-weight: 600;
}
.cuxiaojia {
    font-size: 10px;
    margin-left: 5px;
    color: #a2a2a2;
    background-color: #ebebeb;
}
.porductType {
    border-bottom: 1px solid #e6e6e6;
}
.product_title {
    font-size: 15px;
    line-height: 20px;
}
.pt_label {
    font-size: 10px;
    display: inline-block;
    padding: 0px 6px;
    margin: 8px 0px;
    background-color: #ff0036;
    color: #fff;
}
.buy {
    background-color: #ff0036;
    padding: 12px 0px;
}
.join_shopCar {
    background-color: #fe9402;
    padding: 12px 0px;
}
footer {
    position: fixed;
    width: 100%;
    bottom: 0px;
}
footer div {
    text-align: center;
    background-color: #fff;
}
.Collection {
    color: #5f646e;
}
.parameter {
    width: 100%;
    padding: 5px;
    font-size: 13px;
}
.parameterType {
    border-top: 1px solid #f4f4f4;
    padding: 15px 0px;
}
.parameter_title {
    width: 25%;
    color: #a2a2a2;
}
.parameter_Detalis {
    width: 75%;
    color: #a2a2a2;
}
.products_reviews_type {
    text-decoration: none;
}

.products_reviews_type span {
    display: inline-block;
    font-size: 11px;
    color: #666;
    padding: 8px;
    margin-top: 5px;
    border-radius: 10px;
}
/*  */
.high_opinion {
    background-color: #fee;
}
/*  */
.negative {
    background-color: #f5f5f5;
}
</style>



