import {
  Indicator,
  Toast,
  MessageBox
} from 'mint-ui'

export default {
  openIndicator: function () {
    Indicator.open()
  },
  closeIndicator: function () {
    Indicator.close()
  },
  openToast: function (message, position, duration) {
    if (!message || message === '') {
      message = ''
    }
    if (!position || position === '') {
      position = 'middle'
    }
    if (!duration || duration === '') {
      duration = 1000
    }
    var toast = Toast({
      message: message,
      position: position,
      duration: duration
    })
    return toast
  },
  openMessage: function (title, message, showCancelButton, cb) {
    MessageBox({
      title: title,
      message: message,
      showCancelButton: showCancelButton
    }).then(action => {
      if (action === 'confirm') {
        cb()
      }
    })
  },
  openAlert: function (message, title) {
    MessageBox.alert(message, title)
  }
}



