<template>
    <div>
        <mt-header fixed title=""></mt-header>
        <div class="content">

            <div class="">
                <!-- <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore"> -->

                <!-- <router-link to="order">11111</router-link> -->
                <mt-swipe :auto="4000">

                    <mt-swipe-item v-for="items in swipeItems">
                        <!-- <img :src="items.src" alt=""> -->
                        <div class="swipeIcon" :style="{backgroundImage:'url('+items.src+')'}"></div>
                    </mt-swipe-item>

                </mt-swipe>
                <div class="nav">

                    <div class="navIcon" v-for="items in navItems">
                        <!-- <div class=""></div> -->
                        <img :src="items.src" alt="">
                        <span>{{items.text}}</span>
                    </div>

                </div>

                <img src="../../assets/indexBanner.jpg" alt="">

                <!-- </mt-loadmore> -->
                <div class="activityTitle"></div>
                <!--  -->
                <div style="overflow: hidden;">
                    <productList v-for="items in productItems" :title="items.title" :id="items.id" :imageSrc="items.src" v-on:my-event="productDetil"></productList>
                </div>

            </div>
        </div>
    </div>
</template>
<script>
/*
    
 */
import productList from "../../components/productList";
export default {
    name: "m-home",
    components: {
        //components
        productList
    },
    data() {
        return {
            allLoaded: false, //falsetrue
            scrollMode: "auto", //touchauto
            swipeItems: [
                {
                    src: require("../../assets/swipe2.jpg"),
                    id: "1"
                },
                {
                    src: require("../../assets/swipe2.jpg"),
                    id: "2"
                },
                {
                    src: require("../../assets/swipe2.jpg"),
                    id: "3"
                }
            ],
            navItems: [
                {
                    src: require("../../assets/navIcon.png"),
                    id: "1",
                    text: ""
                },
                {
                    src: require("../../assets/navIcon2.png"),
                    id: "2",
                    text: ""
                },
                {
                    src: require("../../assets/navIcon3.png"),
                    id: "3",
                    text: ""
                },
                {
                    src: require("../../assets/yunshu.png"),
                    id: "4",
                    text: ""
                },
                {
                    src: require("../../assets/navIcon4.png"),
                    id: "5",
                    text: ""
                }
            ],
            //
            productItems: [
                {
                    src: require("../../assets/erji.jpg"),
                    title: "Beats",
                    id: "1"
                },
                {
                    src: require("../../assets/xiezi.jpg"),
                    title: "",
                    id: "2"
                },
                {
                    src: require("../../assets/xiezi.jpg"),
                    title: "",
                    id: "3"
                },
                {
                    src: require("../../assets/erji.jpg"),
                    title: "Beats",
                    id: "4"
                },
                {
                    src: require("../../assets/xiezi.jpg"),
                    title: "",
                    id: "3"
                },
                {
                    src: require("../../assets/erji.jpg"),
                    title: "Beats",
                    id: "4"
                }
            ]
        };
    },
    methods: {
        productDetil(id) {
            // console.log(id);
            this.$router.push({
                path: "page/product_details"
            });
        }
    }
};
</script>
<style scoped>
.content {
    background-color: #fff;
}
img {
    display: block;
    width: 100%;
}
.mint-swipe {
    height: 160px;
}
.mint-swipe-indicators {
    bottom: 20px;
}
.swipeIcon {
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}
.nav {
    display: flex;
    padding-bottom: 10px;
    border-bottom: 1px solid #f7f7f7;
}
.nav .navIcon img {
    display: block;
    width: 50%;

    margin: 0 auto;
    margin-top: 10px;
}
.nav div {
    flex-grow: 1;
}
.nav span {
    display: inline-block;
    width: 100%;
    text-align: center;
    font-size: 13px;
    margin-top: 5px;
}
.activityTitle {
    color: #ff6a69;
    font-weight: bold;
    font-size: 13px;
    text-align: center;
    margin-top: 10px;
}
</style>



