<template>
    <div>
        <!-- <div class="mt-10 bc-fff" v-for="(items,index) in orderList"> -->
        <div class="mt-10 bc-fff">
            <div class="df f-ac p-10">
                <div class="icon-shop c-ff0036"></div>
                <div class="fs-13">{{shopName}}</div>
            </div>
            <router-link to="../product_details" class="df orderList" v-for="items in goodsItems">

                <div class="orderImage fg-1">
                    <div class="bc-img-c" :style="{backgroundImage:'url('+items.image+')'}"></div>
                </div>
                <div class="orderDeilt fg-3 fs-13 ml-10">
                    <div class="fs-13">{{items.name}}</div>
                </div>
                <div class="orderPric fg-1 fs-13 ml-10">
                    <div class="fs-13">{{items.pric}}</div>
                </div>
            </router-link>
            <div class="fs-12 tx-r p-10">{{goodsLength}}
                <span>{{totalPrice}}</span>(5.00)
            </div>
            <div class="orderStateBtn tx-r fs-13 p-10">
                <span class="" @click=""></span>
                <span class="" @click=""></span>
                <span class="take_goods" :data-id="orderId" @click="$emit('take-goods',orderId)"></span>
                <!-- <span></span> -->
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "orderList",
    props: {
        index: {
            type: Number,
            default: 0
        },
        shopName: {
            type: String,
            default: ""
        },
        goodsLength: {
            type: Number,
            default: 0
        },
        totalPrice: {
            type: Number,
            default: 0
        },
        goodsItems: {
            type: Array
        },
        orderId:{
            type:Number,
            default:0
        }
    },
    data() {
        return {
        };
    },
    methods: {
        restNum(index){
            return index
        }
    }
};
</script>
<style scoped>
.orderImage {
    width: 25%;
}
.orderImage div {
    width: 6.2rem;
    height: 6.2rem;
    margin: 0 auto;
}
.orderList {
    padding: 5px 5px;
    background-color: #f5f5f5;
    border-bottom: 3px solid #fff;
}
.orderDeilt {
    width: 60%;
    background-color: #f5f5f5;
}
.orderPric {
    width: 15%;
}
.orderStateBtn span {
    display: inline-block;
    border: 1px solid #e1e1e1;
    padding: 6px;
}
.take_goods {
    border-color: #ff0036 !important;
    color: #ff0036;
}
</style>



