import Vue from 'vue'
import Router from 'vue-router'

// import home from '@/components/home'
import indexTB from '@/page/indexTB'
// import home from '@/page/home'
// import home from '@/page/home'
// import order from '@/page/order'
import home from '@/page/indexPage/home'
import social from '@/page/indexPage/social'
import Main from '@/page/indexPage/main'
import productDetails from '@/page/product_details'
import goodsComment from '@/page/goods/goods_comment'
import orderList from '@/page/order/orderList'
Vue.use(Router)
export default new Router({
  routes: [{
    path: '/',
    redirect: '/home',
    component: indexTB,
    children: [{
      path: '/home',
      name: 'home',
      component: home
    }, {
      path: '/social',
      name: 'social',
      component: social
    }, {
      path: '/Main',
      name: 'Main',
      component: Main
    }]
  }, {
    path: '/page/product_details',
    name: 'product_details',
    component: productDetails

  }, {
    path: '/page/goods/goods_comment',
    name: 'goodsComment',
    component: goodsComment

  }, {
    path: '/page/order/orderList',
    name: 'orderList',
    component: orderList

  }]
})



