
<template>
    <div class="">
        <mt-header fixed title="">
            <div v-on:click="$router.back()" slot="left">
                <mt-button icon="back"></mt-button>
            </div>
        </mt-header>
        <div class="orderTypeContent df">
            <div class="fg-2 p-10-15 fs-13 c-303030" v-on:click="orderSelect(items.type)" v-bind:class="{typeActive:current == items.type}" v-for="(items,index) in orderType">{{items.orderName}}</div>
        </div>
        <!-- :top-method="loadTop" :bottom-method="loadBottom" :auto-fill="false" :bottom-all-loaded="allLoaded" ref="loadmore" -->
        <div class="">
            <order-list v-for="(items,index) in orderItems" :orderId="items.id" :shopName="items.shopName" :goodsItems="items.goods" :index="index" :goodsLength="items.goods.length" v-on:take-goods="takeGoods">

            </order-list>
        </div>
    </div>

</template>

<script>
import orderlist from "../../components/orderList_comp";
export default {
    name: "orderList",
    components: {
        "order-list": orderlist
    },
    data() {
        return {
            allLoaded: false,
            current: 0,
            orderTotalPrice: 0,
            orderType: [
                {
                    orderName: "",
                    className: "icon-card",
                    type: 0
                },
                {
                    orderName: "",
                    className: "icon-yushu",
                    type: 1
                },
                {
                    orderName: "",
                    className: "icon-fahuo",
                    type: 2
                },
                {
                    orderName: "",
                    className: "icon-message2",
                    type: 3
                }
            ],
            //
            orderItems: [
                {
                    id: 1,
                    shopName: "",
                    goods: [
                        {
                            name:
                                "Nintendo SwitchNS",
                            image: require("../../assets/switch.png"),
                            pric: 1205.22
                        },
                        {
                            name:
                                "Nintendo SwitchNS",
                            image: require("../../assets/erji.jpg"),
                            pric: 1205
                        }
                    ]
                },
                {
                    id: 2,
                    shopName: "",
                    goods: [
                        {
                            name:
                                "Nintendo SwitchNS",
                            image: require("../../assets/xiezi.jpg"),
                            pric: 1205
                        }
                    ]
                }
            ]
        };
    },
    created() {
        this.current = this.$route.query.type;
    },
    methods: {
        loadTop: function() {},
        loadBottom: function() {
            // this.$refs.loadmore.onBottomLoaded();
        },
        orderSelect: function(type) {
            this.current = type;
        },
        total_price(idx) {
            var orderItems = this.orderList[idx].goods;
            var totalRrice = 0;
            for (var i = 0; i < orderItems.length; i++) {
                totalRrice += orderItems[i].pric;
            }
            return totalRrice.toFixed(2);
        },
        takeGoods() {
            var common = this.common;
            common.openMessage("", "", true, function() {
                common.openIndicator();
                setTimeout(function() {
                    common.closeIndicator();
                    common.openAlert("","");
                }, 2000);
            });
        }
    }
};
</script>

<style scoped>
.orderTypeContent {
    margin-top: 45px;
    background-color: #fff;

    text-align: center;
}
.typeActive {
    color: #ff0036;
    border-bottom: 2px solid #ff0036;
}
.orderContent {
    margin-top: 45px;
}
</style>



